.wrapper { 
  min-height: 100%;
  position: relative;
  overflow: hidden;
  }
  
  .cityname{
    margin: 0px 0px 10px 0px;
  }

  .weather-main{
   display: flex;
   flex-direction: column;
   align-items: center;
  }


  .title{
    margin: 10px 0px 10px 0px;
  }

  .title,
  .cityname{
    color: #fff;
    font-size: 32px;
    font-weight: 500;
    text-align: center;
    text-shadow: 3px 3px rgba(130, 130, 148, 0.5);
  }

  .cityname{
    margin: 0px 0px 20px 0px;
    
  }
  .weather-date{
    margin: 0px 0px 10px 0px;
  }

  .weather_report{
    display: flex;
    flex-wrap: wrap;
  }

  .sorry{
    color: #fff;
    font-size: 25px;
    margin: 20px 0px 0px 0px;
    
  }

  @media(max-width:500px) {
    .weather_report {
        justify-content: center;
    }
}

  @media(max-width:800px) {
    .weather_report {
        margin: 0px 0px 0px 10px;
        
    }
}