.infoblock{
    display: flex;
    flex-direction: column;
    margin: 0px 10px 0px 10px;
    padding: 5px 0px 5px 0px;
    background-color: rgba(185, 173, 173, 0.2);
    border-radius: 10px;
    
}

.infoblock:first-child{
    background-color: rgba(185, 173, 173, 0.377);
    box-shadow: 5px 5px 5px rgb(175, 102, 53);
}

@media(max-width:500px) {
    .infoblock {
        width: 80%;
        margin: 0px 0px 20px 0px;      
    }
}

@media(max-width:800px) {
    .infoblock {
        margin: 0px 20px 20px 0px;      
    }
}

.time{
 color: #fff;
 font-size: 20px;
 font-weight: 300;
 font-style: italic;
 text-align: center;
 text-shadow: 2px 2px rgba(130, 130, 148, 0.5);
}

.icon {
display: flex;
justify-content: center;
}

.temp{
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    padding: 15px 25px;
    margin: 0px 5px 7px 5px;
    color: #fff;
    font-size: 60px;
    font-weight: 900;
    text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
    text-align: center;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.like,
.humid,
.wind,
.cloud{
    color: #fff;
    font-size: 20px;
    font-weight: 500;
    margin: 10px 4px 0px 4px;
}